"use client";

import { Settings, Edit2, ChevronRight, MapPin, Star, Zap, User, Filter, MessageSquare, Bell, Compass } from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";

// Datele utilizatorului
const userData = {
  name: "User Test",
  role: "Web designer",
  location: "Iași , România",
  rating: "4.9",
  description: "I work in Figma and have an empathetic, clarity-focused approach. Open to exchanging skills related to design, research, or usability testing.",
  skills: ["Wireframing", "Figma", "User Flows", "UX Research"],
  lookingToLearn: ["Motion Design", "Web Development", "Branding"],
  progress: 75,
  level: "Level 4",
};

// --- Sub-componente Refinite ---

// 1. Componentă pentru Badge-uri (Skills & Learning)
interface SkillBadgeProps {
  skill: string;
  variant: 'skill' | 'learning';
  onClick?: () => void;
}

const SkillBadge = ({ skill, variant, onClick }: SkillBadgeProps) => (
    <Badge 
      // Folosim variante Tailwind definite sau culori custom pentru a menține stilul din Flutter
      className={`rounded-xl px-3 py-1 font-medium text-sm transition-colors 
        ${variant === 'skill' ? 'bg-[#24507f] text-white hover:bg-[#24507f]/90' : 'bg-primary text-white hover:bg-primary/90'}
      `}
      onClick={onClick}
    >
      {skill}
    </Badge>
);

// 2. Componentă pentru Butoanele de Acțiune de pe Pagină
interface ProfileActionButtonProps {
  icon: React.ComponentType<{ className?: string }>;
  text: string;
  onClick: () => void;
}

const ProfileActionButton = ({ icon: Icon, text, onClick }: ProfileActionButtonProps) => {
  return (
    <Button
      variant="ghost"
      // Folosim o clasă semi-transparentă care se apropie de 0x8C8B9AB7
      className="w-full justify-start h-12 rounded-xl px-4 bg-muted/50 hover:bg-muted/70 text-primary transition-colors"
      onClick={onClick}
    >
      <div className="flex items-center space-x-4">
        <Icon className="h-5 w-5 text-primary" />
        <span className="text-base font-medium">{text}</span>
      </div>
      <ChevronRight className="h-5 w-5 ml-auto text-primary" />
    </Button>
  );
};

// 3. Componentă pentru Bara de Navigare Inferioară
type NavItemProps = {
  icon: React.ComponentType<{ className?: string }>;
  label: string;
  isActive: boolean;
};

const NavItem = ({ icon: Icon, label, isActive, onClick }: NavItemProps & { onClick?: () => void }) => (
    <div className={`flex flex-col items-center p-1 cursor-pointer transition-colors ${isActive ? 'text-primary' : 'text-gray-500'}`}>
        <Icon className="h-6 w-6" />
        <span className="text-xs mt-0.5">{label}</span>
    </div>
);

// --- Componenta Principală ---

export default function ProfileScreenRefined() {
  const handleAction = (action: string) => console.log(`${action} clicked`);

  return (
    // Setăm fundalul pentru a simula ecranul mobil
    <div className="flex min-h-screen justify-center bg-gray-50 pb-20">
      
      {/* Container Principal (simulează ecranul mobil max-w-sm) */}
      <div className="w-full max-w-sm bg-background shadow-xl overflow-y-auto relative">
        
        {/* 1. Banner (Fără poziționare absolută, se folosește fluxul normal) */}
        <div 
          className="h-64 w-full bg-cover bg-center relative" 
          style={{ backgroundImage: `url('https://placehold.co/390x256/1d324e/ffffff?text=Banner+Image')` }}
        >
          {/* Poziționat(305, 50) - Iconiță Edit/Settings (dacă este necesară) */}
          <div className="absolute top-4 right-4 p-2 rounded-full bg-white/20 backdrop-blur-sm cursor-pointer">
            <Settings className="h-6 w-6 text-white" />
          </div>
        </div>

        {/* Conținutul Profilului (începe în zona banner-ului) */}
        <div className="relative p-6 -mt-20"> {/* -mt-20 pentru suprapunerea avatarului */}
          
          {/* 2. Avatar și Info de bază */}
          <div className="flex flex-col items-center">
            <Avatar className="h-40 w-40 border-4 border-background shadow-lg">
              <AvatarImage src="/placeholder-avatar.jpg" alt={userData.name} />
              <AvatarFallback className="bg-secondary text-white font-bold text-4xl">UT</AvatarFallback>
            </Avatar>
            
            <h1 className="text-2xl font-bold text-primary mt-4">{userData.name}</h1>
            <p className="text-sm text-muted-foreground mt-1">{userData.role}</p>

            {/* Locație și Rating */}
            <div className="flex items-center space-x-4 mt-3">
                <div className="flex items-center space-x-1 text-base text-primary">
                    <MapPin className="h-4 w-4 text-muted-foreground" />
                    <span>{userData.location}</span>
                </div>
                
                {/* Rating (Card mic) */}
                <Card className="flex items-center space-x-1 p-2 bg-card rounded-full shadow-md border-none">
                    <Star className="h-4 w-4 text-yellow-500 fill-yellow-500" />
                    <span className="text-base font-medium text-primary">{userData.rating}</span>
                </Card>
            </div>
          </div>
          
          {/* 3. Descriere Card */}
          <Card className="mt-8 p-4 bg-muted/40 rounded-xl border-none shadow-none">
            <p className="text-base font-semibold text-primary mb-2">Description:</p>
            <p className="text-sm text-primary/90">
              {userData.description}
            </p>
          </Card>

          {/* 4. Skills Card */}
          <Card className="mt-6 p-4 bg-muted/40 rounded-xl border-none shadow-none">
            <div className="flex justify-between items-start mb-3">
              <h3 className="text-base font-semibold text-primary">Skills:</h3>
              <Button size="icon" className="h-8 w-8 rounded-lg bg-[#24507f] hover:bg-[#24507f]/90">
                <span className="text-xl font-bold">+</span>
              </Button>
            </div>
            <div className="flex flex-wrap gap-2">
              {userData.skills.map((skill: string) => (
                <SkillBadge key={skill} skill={skill} variant="skill" />
              ))}
            </div>
          </Card>

          {/* 5. Looking to learn Card */}
          <Card className="mt-6 p-4 bg-muted/40 rounded-xl border-none shadow-none">
            <div className="flex justify-between items-start mb-3">
              <h3 className="text-base font-semibold text-primary">Looking to learn:</h3>
              <Button size="icon" className="h-8 w-8 rounded-lg bg-[#24507f] hover:bg-[#24507f]/90">
                <span className="text-xl font-bold">+</span>
              </Button>
            </div>
            <div className="flex flex-wrap gap-2">
              {userData.lookingToLearn.map((skill: string) => (
                <SkillBadge key={skill} skill={skill} variant="learning" />
              ))}
            </div>
          </Card>

          {/* 6. Progress Card (Level 4) */}
          {/* Folosim un bg-blue-100 ca echivalent pentru 0x2694C6E7 */}
          <Card className="mt-6 p-6 bg-blue-100/50 rounded-xl border-none shadow-sm">
            <div className="flex items-center justify-between">
                <div className="text-sm font-semibold text-primary">Progress</div>
                <Badge className="px-2 py-0.5 rounded-md font-semibold text-yellow-600 bg-transparent border-none">
                    {userData.level}
                </Badge>
            </div>

            <div className="flex flex-col items-center my-6">
                 {/* Placeholder pentru ilustrația copac (128x128) */}
                 <div className="w-32 h-32 bg-green-200 rounded-full flex items-center justify-center">
                    <Zap className="h-10 w-10 text-green-700" />
                 </div>
            </div>

            <div className="space-y-2">
              <div className="relative h-4 rounded-xl bg-gray-300">
                  <Progress 
                    value={userData.progress} 
                    // Folosim o clasă custom pentru bara de progres cyan 0xFF40C9E7
                    className="h-full bg-[#40C9E7] rounded-xl [&>*]:bg-[#40C9E7]" 
                  />
                  {/* Punctul rotativ de la 75% */}
                  <div 
                    className="absolute top-1/2 -translate-y-1/2 w-5 h-5 bg-white rounded-full border border-[#40C9E7] shadow-md"
                    style={{ left: `calc(${userData.progress}% - 10px)` }}
                  ></div>
              </div>
              <div className="flex justify-between text-xs text-primary font-medium">
                  <span>Progress</span>
                  <span>{userData.progress}%</span>
              </div>
            </div>
          </Card>


          {/* 7. Butoane de Navigare inferioare (Settings, Edit Profile) */}
          <div className="mt-8 space-y-4 mb-20"> 
            <ProfileActionButton 
              icon={Settings} 
              text="Settings" 
              onClick={() => handleAction("Settings")}
            />
            <ProfileActionButton 
              icon={Edit2} 
              text="Edit profile" 
              onClick={() => handleAction("Edit profile")}
            />
          </div>
          
        </div>
      </div>
      
      {/* 8. Bara de Navigare Inferioară (BottomNavBar) */}
      <div className="fixed bottom-0 left-0 right-0 max-w-sm mx-auto h-16 bg-white border-t border-gray-300 shadow-lg flex justify-around items-center">
        <NavItem icon={Compass} label="Discover" isActive={false} />
        <NavItem icon={Filter} label="Filter" isActive={false} />
        <NavItem icon={MessageSquare} label="Chat" isActive={false} />
        <NavItem icon={Bell} label="Notify" isActive={false} />
        <NavItem icon={User} label="Profile" isActive={true} />
    </div>

    </div>
  );
}